@import "variables";

//SOF Header Menu scss
.main_bdy_cls{
    display: flex;
    flex-direction: column;
    color: $font-color;
    height: 100%;
}

.main_flx_blk{
    flex: 1;
    overflow: auto;
}
.header_parnt_blk{
    background: rgb(33,37,41);
    padding: 8px 0;
    .dropdown-toggle::after{
        border-top: 0.3em solid $white-color;
    }
}
header .logo_blk{
    height: 100%;
    padding: 0 !important;
    .curnt_outlet{
        line-height: normal;
    }
}
.header_parnt_blk .dotted_border{
    position: relative;
    top: 2px;
}
.dotted_border{
    width: 1px;
    height: 25px;
    background-color: rgb(128, 128, 128);
    margin: 0 10px;
}

.header_search{
    width: 100%;
    float: right;
    height: 34px;
    font-family: $main-font;
    border: 1px solid #555c63;
    background-color: #343a40;
    color: #fff;
}
.sub_header_parnt{
    background-color: $theme-color-blue;
    padding: 0;
    height: auto;
    .busnss_date{
        display: none;
    }
    &.navbar-dark .navbar-nav .nav-link.active {
        background-color: $nav-highlight;
        color: $theme-color-blue;
    }
    &.navbar-dark .navbar-nav .nav-link:hover {
        background-color: $nav-highlight;
        transition: all ease .3s;
        color: $theme-color-blue;
    }    
    
    &.navbar-dark .navbar-nav .nav-link{
        color: #ffffff;
        font-size: 0.875rem;
        display: inline-block;
    }
}
.dropdown-menu{
    padding: 0;
}
.dropdown-item.active,.dropdown-item:active{
    background-color: $theme-color-blue !important;
}
.navbar-nav .dropdown-menu{
    border: none;
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0 0 4px 4px;
}
.navbar-dark .navbar-toggler-icon{
    background-image:url("data:image/svg+xml,<svg viewBox='0 0 16 16' fill='%23fff' xmlns='http://www.w3.org/2000/svg'><path fill-rule='evenodd' d='M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z' clip-rule='evenodd'/></svg>"); 
}
.busnss_date{
    margin-right: 10px;
    input{
        display: none;
        width: 100%;
    }
    label{
        font-size: 0.875rem;
        color: #ffffff;
        i{
            margin-right: 5px;
            font-size: 20px;
        }
    }
    &.busnss_mob_resp{
        display: none;
    }
}

.navbar-nav li ul li:hover > ul.dropdown-menu {
    display: block;
}
.dropdown-submenu {
    position:relative;
}
.dropdown-submenu > .dropdown-menu {
    top: 0;
    left: 100%;
    // margin-top:-6px;
}

.dropdown-menu > li > a:hover:after {
    transform: rotate(-90deg);
} 

.dropdown-menu[data-bs-popper]{
    margin-top: 0 !important;
}

.dropdown-item{
    padding: 9px 10px;
}
//EOF Header Menu scss

.blk_bg_clr{
    padding-top: 15px;
}

.rmg_lst_btn, .rmg_lst_mdl{
    position: relative;
    width: 40px;
    height: 40px;
    background-color: #4F5A65;
    padding: 0 !important;
    border-radius: 50% !important;
    i{
        font-size: 21px;
        color: $white-color;
        margin-right: 0;
    }
    &:hover{
        background-color: #00365c;
    }
}
.srch_btn{
    position: relative;
    width: 40px;
    height: 40px;
    background-color: $theme-color-blue;
    padding: 0 !important;
    border-radius: 50% !important;
    i{
        font-size: 21px;
        color: $white-color;
        margin-right: 0;
    }
    &:hover{
        background-color: #00365c;
    }
}

.downld_file, .del_rnd_btn,.add_btn{
    position: relative;
    width: 40px;
    height: 40px;
    margin-left: auto;
    background-color: #6c757d;
    padding: 0 !important;
    border-radius: 50% !important;
    i{
        font-size: 21px;
        color: $white-color;
        margin-right: 0 !important;
    }
    &:hover {
        background-color: #00365c;
    }
}

.del_rnd_inp_btn{
    width: 40px;
    height: 40px;
    background-color: #f2faff;
    border-radius: 50%;
    padding: 0;
    transition: all ease 0.3s;
    box-shadow: inset 0 0 1px 1px #dcf1ff;
    color: #135B8F;
    &:hover, &:focus{
        background-color: $theme-color-blue;
        color: $white-color;
        box-shadow: inset 0 0 1px 1px $theme-color-blue;
    }
}
.add_btn i{
    font-size: 13px;
}
.title_blk .add_btn i{
    font-size: 21px;
}
.grd_add_btn{
    position: relative;
    width: 40px;
    height: 40px;
    background-color: #6c757d;
    border-radius: 50%;
    float: right;
    top: 5px;
    right: 10px;
    &::after{
        position: absolute;
        content: "\F4FE";
        font-family: "Bootstrap-icons";
        font-size: 21px;
        line-height: 40px;
        color: $white-color;
        right: 0;
        top: 0;
        left: 0;
    }
    &:hover, &:focus{
        background-color: #5d6770;
    }
}
.title_blk {
    display: flex;
    align-items: center; 
    margin-bottom: 5px;
    padding-bottom: 10px;
    &.title_brd{
        border-bottom: 2px solid $border-color;
        // padding-bottom: 10px;
        .tabs_blk{
            margin-left: 10px;
            .nav-tabs{
                border-bottom: none;
            }
            .nav-link{            
                padding: 10px 12px;
                border-radius: $border-radius $border-radius 0 0;
                border-right: none;
                &.active{
                    border: 1px solid $theme-color-blue;
                }
                label{
                    cursor: pointer;
                }
            }
            
        } 
    }
    h2{
        font-size: 1.25rem;
        line-height: 1.4rem;
        position: relative;
        margin: 0;
        border-radius: 0;
        color: $font-color;
    }
    i{
        margin-right: 5px;
    }
}

.left_arrow_icn,.edit_btn_icn{
    font-size: 1rem;
}

.check_in_icn{
    font-size: 1rem;
    line-height: 1.2rem;
}

.cash_btn_icn{
    font-size: 1rem;
    line-height: 1.2rem;
}
.anchr_clr{
    color: #2b46b7 !important;
    cursor: pointer;
}
.blk_pstn_abslt{
    position: absolute;
    bottom: 5px;
    border-top: none;
    padding: 0;
    margin: 0;
}
.showIcon, .printIcon,.modifyIcon,.grpcheckinIcon,.locationIcon, .depositIcon, .searchIcon, .messageIcon, .tickIcon{
    display: inline-block;
    padding: 2px 4px;
    margin-right: 5px;
