.report_blk{
    position: relative;
    .demo-container{
        overflow: auto;
    }
    .prview_blk{
        position: relative;
        height: 610px;
        z-index: 0;
        width: 100%;
        overflow: auto;
    }
    h3{
        font-size: 1rem;
    }
    .stiJsViewerParentMenu{
        width: auto !important;
        display: inline-block;
        background-color: #fff;
    }
    .stiJsViewerReportPanel{
        z-index: -1;
        top: 41px !important;
    }
    .stiJsViewerClearAllStyles{
        font-size: 0.8125rem;
    }
    .stiJsViewerPageShadow{
        margin: 0 !important;
    }
}

.futr_aval_details{
    text-align: center;
    margin-top: 10px;
    button{
        width: 40px;
        height: 40px;
        padding: 0;
        &:first-child{
            margin-right: 5px;
        }
        &:last-child{
            margin-left: 5px;
        }
        i{
            font-size: 40px;
            line-height: 40px;
            color: #6c757d;
        }
        &:hover i{
            color: #00365c;
        }
    }
}

.tabs_blk .tab-content{
    padding: 10px 0;
}
.tabs_blk .nav-link{
    color: $font-color;
    background-color: #f9f9f9;
    border-color: #dee2e6;
    font-size: 1rem;
    padding: 10px 12px;
    border-radius: 4px 4px 0 0;
    &.no-click-tab-btn{
        background-color: transparent;
        padding-left: 0;
        padding-right: 0;
        border: none;
        border-right: none !important;
        
    }
    i{
        font-size: 19px;
        line-height: 20px;
    }
    svg{
        fill: #000;
        position: relative;
        top: -3px;
        height: 20px;
        &.vst_detls_icn{
            width: 31px;
        }
        &.arrvl_dprt_icn{
            width: 37px;
        }
    }    
    label{
        cursor: pointer;
    }
}
.tabs_blk .nav-item:last-child .nav-link{
    border-right: 1px solid #dee2e6;
}
.tabs_blk .nav-tabs .nav-link.no-click-tab-btn:hover, .tabs_blk .nav-tabs .nav-link.no-click-tab-btn:focus,.tabs_blk .nav-tabs .nav-link.active.no-click-tab-btn{
